
#app {
  position: relative;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#bg {
  width: 100%;
  height: 100%;
  position: fixed;
  -webkit-filter: blur(4px);
          filter: blur(4px);
  z-index: -1000;
}
.showTab {
  position: absolute;
  z-index: 10000;
  right: 20px;
  top: 20px;
  width: 40px;
  height: 40px;
  background: #E74C3C;
  color: #fff;
  text-align: center;
  line-height: 38px;
  border-radius: 50%;
  font-size: 22px;
  box-shadow: #000 0 0 5px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(.77, .14, .16, .78);
}
.body {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100%;
          flex: 0 0 100%;
  padding: 100px 0;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  transition: all 0.5s cubic-bezier(.77, .14, .16, .78);
  -webkit-transform: translateX(0);
          transform: translateX(0);
  background-size: cover;
}
.body .input .innerinput {
  position: relative;
  top: 10%;
  width: 40%;
  height: 34px;
  margin: 0 auto;
}
.body .input .innerinput img {
  position: absolute;
  left: 10px;
  top: 2px;
  z-index: 10;
}
.body .input input {
  padding: 0 50px;
  width: 100%;
  height: 34px;
  border: none;
  border-radius: 16px;
}
.body .input input:focus {
  outline: none;
}
.body .input .search {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 100%;
  border-radius: 0 17px 17px 0;
  background: #2ECC71;
  color: #fff;
  text-align: center;
  line-height: 34px;
  font-size: 14px;
  text-decoration: none;
}
.tab {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 316px;
          flex: 0 0 316px;
  width: 316px;
  position: relative;
  background: #fff;
  transition: all 0.5s cubic-bezier(.77, .14, .16, .78);
  -webkit-transform: translateX(-300px);
          transform: translateX(-300px)
}
.tab .select {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  background: #2CCE71;
}
.tab .select span.active{
  color: red;
}
.tab .select span {
  display: inline-block;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 33%;
          flex: 0 0 33%;
  color: #fff;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
}
.tab .select span i{
  font-size: 20px;
}
.tab .select span:hover{
  color: red;
}
.slideTab-enter-to,
.slideTab-leave {
  -webkit-transform: translateX(-300px);
          transform: translateX(-300px)
}
.slideTab-enter,
.slideTab-leave-to {
  -webkit-transform: translateX(0);
          transform: translateX(0)
}
.tab-body{
  width: 99%;
  height: 100%;
  overflow: auto;
}
.tab-body .shuqian-body .bookmarks{
  width: 92%;
  padding: 15px 0;
}
.tab-body .shuqian-body .bookmarks .list{
  position: relative;
  padding-left: 20px;
  width: 100%;
  height: 30px;
}
.tab-body .shuqian-body .bookmarks .list a{
  display: inline-block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  font-size: 12px;
  color: #000;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.tab-body .shuqian-body .bookmarks .list a span{
  display: inline-block;
  height: 100%;
  line-height: 30px;
}
.tab-body .shuqian-body .bookmarks .list span.close{
  display: none;
  position: absolute;
  right: 5px;
  top: 5px;
  width: 20px;
  height: 20px;
  transition: all 0.5s linear;
  background: #DD0000;
  color: #fff;
  text-align: center;
  line-height: 20px;
  border-radius: 50%;
  cursor: pointer;
}
.tab-body .shuqian-body .bookmarks .list:hover span.close{
  display: inline-block;
}
.bgMoveOut {
  -webkit-transform: translateX(-180px);
          transform: translateX(-180px);
}
.showTab.active {
  right: 10px;
  top: 5px;
  -webkit-transform: rotate(405deg) scale(0.8);
          transform: rotate(405deg) scale(0.8);
}
.fengche {
  position: absolute;
  bottom: 0;
  right: 50px;
  width: 40px;
  height: 65px;
  cursor: pointer;
}
.fengche img.active {
  transition: all 0.5s cubic-bezier(.77, .14, .16, .78);
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.fengche img {
  position: absolute;
  top: 0;
  z-index: 3;
}
.fengche .fengchegan {
  position: absolute;
  bottom: 0;
  right: 15px;
  width: 5px;
  height: 40px;
  background: #AAA;
  z-index: 2;
}



